<template>
  <div>
    <div class="m-container">
      <div class="bar"></div>
      <p class="title">{{title}}</p>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  props: {
    title: {
      type: String,
      default: ''
    }
  }
}
</script>
<style lang="stylus" scoped rel="stylesheet/stylus">
@import "~common/stylus/base"
@import "~common/stylus/variable"
.m-container
  @extend .flex
  @extend .flex-row
  @extend .flex-start
  width: 100%
  height: 40px
  .bar
    width: 8px
    height: 20px
    background-color: $color-back-blue_0
    margin-right: 15px
  .title
    color: $color-back-blue_0
    font-size: $size-14
</style>


